
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
nav{
  text-align: center;
}
nav .brand-logo {
  display: inline-block;
  text-align: center;
  position: relative;
}
.input-field label {
  color: white;
}

.input-field input[type=text]:focus + label {
  color: white;
} 

.input-field input[type=text]:focus {
  border-bottom: 1px solid white;
  box-shadow: 0 1px 0 0 #000;
}

input[type=text]:not(.browser-default) {
  border-bottom: 1px solid white;
  color: white;
}
input[type=text]:not(.browser-default):focus:not([readonly]) {
  border-bottom: 1px solid white;
  -webkit-box-shadow: 0 1px 0 0 white;
  box-shadow: 0 1px 0 0 white;
}
input[type=text]:not(.browser-default):focus:not([readonly]) + label {
  color: white;
}
.waves-effect.waves-light.btn-large{
  padding: 0px;
}
input[type=text]{
  padding-left: 12px !important;
}
input[type=submit]{
  display: block;
  width: 100%;
  cursor: pointer;
  height: 100%;
}

.error-p{
  padding: 1rem;
  text-align: center;
  border-radius: 3px;
  color: #f44336 ;
  font-weight: 700;
}

.form-container {
  padding: 3rem 0 1px 0;
  background-color: #ec407a  ;
}
.mensaje {
  text-align: center;
  font-size: 2rem;
}
.buscador i {
  width: 100%;
}
.error {
  padding: 1rem!important;
  color:white;
  margin-top: 3rem;
  text-align: center;
  text-emphasis: left;
}
.card-panel h2 {
  font-size: 1.6rem;
  text-align: center;
}
.card-panel p {
  text-align: center;
  font-size: 1.4rem;
}
.card-panel p.small{
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 0px;
}
.temp {
  font-size: 4rem!important;
  margin-top: 15px;
}
.temp span {
  font-size: 2rem!important;
  vertical-align: super;
}

.btn-block {
  display: block!important;
}

.error {
  padding: 1rem!important;
  text-align: center!important;
  color: #FFF!important;
}

img{
  display: block;
  margin: 0 auto;
}